import { auth } from '@/auth';
import ContentCard from '@/components/ContentCard';
import { mockedContent } from '@/lib/data';
import { fetchLiveNews, fetchHistoricalEvents } from '@/lib/external-data';
import dbConnect from '@/lib/db';
import User from '@/models/User';
import { redirect } from 'next/navigation';
import { CheckCheck } from 'lucide-react';
import SignOutButton from '@/components/SignOutButton';

export default async function HomePage() {
  const session = await auth();
  if (!session?.user?.email) redirect('/login');

  await dbConnect();
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const user: any = await User.findOne({ email: session.user.email });
  
  const savedIds: string[] = user?.savedContentIds || [];
  const seenIds: string[] = user?.seenContentIds || [];

  // Fetch Live Data
  const liveNews = await fetchLiveNews();
  const liveHistory = await fetchHistoricalEvents(); // We might show some history on home too if "fresh"

  // Merge Live Data with Mocked Data (mocked serves as base for other categories like entertainment/tech-tips)
  const allContent = [...liveNews, ...liveHistory, ...mockedContent];

  // Filter Logic:
  // 1. Not in History category (unless it's brand new and we want to tease it? User said "history items, add another tab")
  //    Actually, user said "everything should be of today's news except for the history".
  //    So Home = News (External), Tech, Ent, etc. History = History Tab.
  const freshContent = allContent.filter(item => 
    item.category !== 'History Stories' && 
    !seenIds.includes(item.id)
  );

  // Personalization Logic
  const scores = user?.interactionProfile?.categoryScores || new Map();
  
  const feedContent = freshContent.sort((a, b) => {
    // Boost items published "today" (simple check vs current date string in mock)
    const isTodayA = a.publishDate === new Date().toISOString().split('T')[0] ? 5 : 0;
    const isTodayB = b.publishDate === new Date().toISOString().split('T')[0] ? 5 : 0;

    const scoreA = (scores.get(a.category) || 0) + isTodayA;
    const scoreB = (scores.get(b.category) || 0) + isTodayB;
    
    return (scoreB + Math.random() * 2) - (scoreA + Math.random() * 2);
  });

  return (
    <div className="pt-4 pb-4">
      <header className="mb-6 px-4 flex items-center justify-between">
        <div>
           <p className="text-gray-400 text-xs uppercase tracking-wider font-bold mb-1">Fresh Bites For</p>
           <h1 className="text-3xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-accent-orange to-accent-yellow leading-tight">
             {user?.name || 'You'}
           </h1>
           <p className="text-gray-500 text-xs mt-2 italic">Tap to expand. Once you see it, it's gone!</p>
        </div>
        <SignOutButton />
      </header>
      
      {feedContent.length > 0 ? (
        <div className="flex flex-col gap-2">
          {feedContent.map((item) => (
            <ContentCard
              key={item.id}
              {...item}
              previewMode={true} // Enable preview mode
              isSaved={savedIds.includes(item.id)}
            />
          ))}
        </div>
      ) : (
         <div className="flex flex-col items-center justify-center h-[50vh] text-center px-6 opacity-60">
            <CheckCheck className="w-16 h-16 text-accent-green mb-4" />
            <h3 className="text-xl font-bold text-white mb-2">All caught up!</h3>
            <p className="text-gray-400 text-sm">You've devoured everything new. Check back tomorrow!</p>
        </div>
      )}
    </div>
  );
}
